'use client';

import { useEffect } from 'react';

export function Modal({
  open,
  title,
  children,
  onClose,
}: {
  open: boolean;
  title: string;
  children: React.ReactNode;
  onClose: () => void;
}) {
  useEffect(() => {
    function onKey(e: KeyboardEvent) {
      if (e.key === 'Escape') onClose();
    }
    if (open) window.addEventListener('keydown', onKey);
    return () => window.removeEventListener('keydown', onKey);
  }, [open, onClose]);

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50">
      <div className="absolute inset-0 bg-black/60" onClick={onClose} />
      <div className="absolute inset-0 p-4 sm:p-6 flex items-end sm:items-center justify-center">
        <div className="w-full max-w-lg panel p-5 rounded-2xl shadow-2xl">
          <div className="flex items-center justify-between gap-3">
            <div className="text-lg font-extrabold">{title}</div>
            <button
              onClick={onClose}
              aria-label="Close"
              className="w-9 h-9 grid place-items-center rounded-xl border border-[color:var(--stroke)] bg-[rgba(22,26,34,.55)] text-[color:var(--muted)] hover:text-white"
            >
              <svg width="18" height="18" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M6 6L18 18" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
                <path d="M18 6L6 18" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
              </svg>
            </button>
          </div>
          <div className="mt-4">{children}</div>
        </div>
      </div>
    </div>
  );
}
