'use client';

import { useEffect, useMemo, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { NavShell } from '@/app/components/NavShell';
import { Modal } from '@/app/components/Modal';
import { useWorkspace } from '@/lib/firebase/useWorkspace';
import { subscribeBoard } from '@/lib/firebase/boardsLive';
import { updateBoard } from '@/lib/firebase/boards';

export default function BoardSettingsPage() {
  const params = useParams<{ boardId: string }>();
  const router = useRouter();
  const boardId = decodeURIComponent(params.boardId);
  const { workspaceId } = useWorkspace();

  const [loadedOnce, setLoadedOnce] = useState(false);
  const [board, setBoard] = useState<{ id: string; name: string; description?: string } | null>(null);

  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [confirmOpen, setConfirmOpen] = useState(false);

  const [dirty, setDirty] = useState(false);

  useEffect(() => {
    if (!workspaceId) return;
    const unsub = subscribeBoard(workspaceId, boardId, (b) => {
      const next = b ? { id: b.id, name: b.name, description: b.description } : null;
      setBoard(next);
      setLoadedOnce(true);
      if (next && !dirty) {
        setName(next.name);
        setDescription(next.description ?? '');
      }
    });
    return () => unsub();
  }, [workspaceId, boardId, dirty]);

  const canSave = useMemo(() => !!workspaceId && !!board && name.trim().length > 0, [workspaceId, board, name]);

  if (!workspaceId) {
    return (
      <NavShell title="Board settings">
        <div className="panel p-4 text-[color:var(--muted)]">Loading…</div>
      </NavShell>
    );
  }

  if (loadedOnce && !board) {
    return (
      <NavShell title="Board settings">
        <div className="panel p-4 text-[color:var(--muted)]">Board not found.</div>
      </NavShell>
    );
  }

  if (!board) {
    return (
      <NavShell title="Board settings">
        <div className="panel p-4 text-[color:var(--muted)]">Loading…</div>
      </NavShell>
    );
  }

  async function save() {
    if (!workspaceId || !board) return;
    await updateBoard(workspaceId, boardId, {
      name: name.trim() || board.name,
      description: description.trim() || undefined,
    });
    router.replace('/boards');
  }

  return (
    <NavShell title="Board settings">
      <div className="panel p-5">
        <div className="text-xs uppercase tracking-wide text-[color:var(--muted)] font-extrabold">Name</div>
        <input
          value={name}
          onChange={(e) => {
            setDirty(true);
            setName(e.target.value);
          }}
          className="mt-1 w-full rounded-2xl bg-[rgba(11,12,15,.7)] border border-[color:var(--stroke)] p-3 outline-none"
        />

        <div className="mt-4 text-xs uppercase tracking-wide text-[color:var(--muted)] font-extrabold">Description</div>
        <textarea
          value={description}
          onChange={(e) => {
            setDirty(true);
            setDescription(e.target.value);
          }}
          className="mt-1 w-full min-h-[90px] rounded-2xl bg-[rgba(11,12,15,.7)] border border-[color:var(--stroke)] p-3 outline-none"
        />

        <div className="mt-4 text-xs text-[color:var(--muted)]">
          Board deletion is not implemented yet in Firestore mode (needs server/admin recursive delete of cards).
        </div>

        <div className="mt-5 flex flex-wrap gap-2">
          <button
            onClick={save}
            disabled={!canSave}
            className="btn-primary px-4 py-3 rounded-2xl font-extrabold disabled:opacity-50"
          >
            Save
          </button>
          <button
            onClick={() => setConfirmOpen(true)}
            className="px-4 py-3 rounded-2xl border border-[rgba(225,29,72,.45)] bg-[rgba(225,29,72,.10)] font-extrabold"
          >
            Delete board
          </button>
        </div>
      </div>

      <Modal open={confirmOpen} title="Delete board?" onClose={() => setConfirmOpen(false)}>
        <div className="text-sm text-[color:var(--muted)]">
          Deletion is temporarily disabled until we add a safe server-side recursive delete (board + cards).
        </div>
        <div className="mt-4">
          <button
            onClick={() => setConfirmOpen(false)}
            className="w-full px-4 py-3 rounded-2xl border border-[color:var(--stroke)] bg-[rgba(22,26,34,.55)] font-extrabold"
          >
            Close
          </button>
        </div>
      </Modal>
    </NavShell>
  );
}
