import { doc, collection, onSnapshot, query } from 'firebase/firestore';
import { firestoreDb } from './client';
import type { BoardDoc } from './boards';

export function subscribeBoards(workspaceId: string, cb: (boards: BoardDoc[]) => void) {
  const db = firestoreDb();
  const q = query(collection(db, 'workspaces', workspaceId, 'boards'));
  return onSnapshot(q, (snap) => {
    cb(snap.docs.map((d) => ({ id: d.id, ...(d.data() as Omit<BoardDoc, 'id'>) })));
  });
}

export function subscribeBoard(workspaceId: string, boardId: string, cb: (board: BoardDoc | null) => void) {
  const db = firestoreDb();
  const ref = doc(db, 'workspaces', workspaceId, 'boards', boardId);
  return onSnapshot(ref, (snap) => {
    if (!snap.exists()) return cb(null);
    cb({ id: snap.id, ...(snap.data() as Omit<BoardDoc, 'id'>) });
  });
}
